@import 'normalize';
@import 'variables';
@import 'mixins';
@import 'fonts';
@import 'header';
@import 'socials';
@import 'slick';



* {
    box-sizing: border-box;
}

html {
    font-size: 10px;
    scroll-behavior: smooth;  
}

body {
    @include font($body-ff, 1.6rem, $body-color, $lh: 1.6);
    padding: 0;
    margin: 0;

}

.container {
    outline: 1px solid red;
    margin: 0 auto;
    max-width: $container-width;
    width: 100%;
    // padding: 15px;
    // @media screen and (max-width: 992px) {
    //     max-width: 750px;
    // }
}

.section {
    overflow: hidden;  
    position: relative;
    min-height: 100vh;
    margin-bottom: 20px;
    // padding: 30px 0;
        &__link {
            @include posA ($top: auto, $left: 50%, $delta_y: 0); 
            text-align: center;
            bottom: 10%;
            width: $social-size;
            height: $social-size;
            line-height: calc(#{$social-size} - 2 * #{$border-width});
            display: inline-block;
            border-radius: 50%;
            border: $border-width solid rgba($body-color, .4); 
            z-index: 20;      
            @include hover{
                background-color: $body-color;
                border-color:rgba($body-color, 1);
            }  
            &__icon{
                color: $body-color; 
                @include hover{
                    color: $primary-color; 
                }
            }        
        }
        &__title {
            @include font($title-ff, 4.4rem, $secondary-color, 700, $lh: 2);
            text-transform: uppercase;
            @include clamp;
            text-align: center;
            padding: 20px 0;
            margin-top: 40px;
            position: relative;
            &:before{
                content: '';
                display: block;
                width: 8%;
                height: calc(#{$border-width} * 1);
                background-color: $secondary-color;
                @include posA($top: 0%, $left: 50%, $delta_x: -50%, $delta_y: 0);
            }
            &--white {
                color: $body-color;
            }
            &:before{
                background-color: $body-color;
                
            }
        }
        &__content {
            margin: 80px 0;
        }
        &__content--reverse {
            margin: 140px 0;
        }
        &--bg{
            @include bg;
            padding: 100px 0;
        }
        

        

}

.text {
    &__title {
        @include font($title-ff, 4.4rem, $secondary-color, 700, $lh: 1.6);
        text-transform: uppercase;
        margin: 0 0 20px 0;
        @include clamp($line-clamp: 1);
    }
    &__subtitle {
        @include font($title-ff, 2.4rem, $secondary-color, 400, $lh: 1);
        text-transform: uppercase;
        @include clamp($line-clamp: 1);
    }
    &__info {
        @include font($body-ff, 1.8rem, $grey-color, 400, $lh: 1.6);
        @include clamp($line-clamp: 3);
    }
    &__btn {
        margin: 40px 0 60px 0;
    }
    &__link {
        text-decoration: none;
        @include font($btn-ff, 1.4rem, $secondary-color, 400, $lh: 1);
        cursor: pointer;
        @include tr;
        @include hover {
            color: $primary-color; 
        } 
    }
}
    
.header {
    position: relative;
    background-color: $grey-color;
    margin-bottom: 50px;
    &:after{
        content: '';
        background-image:linear-gradient(45deg, rgba(85,183,255, .8) 0%, rgba(126,90,255,.8) 100%);
        height: 100vh;
        @include posA($top: null, $left: 0, $delta_x: null, $delta_y: null);
        bottom: 0;
        width: 100%;
          
    }
    

    &__bg{
        @include posA;
        min-width: 100%;
        height: 100vh;
        }
}

.header-slider{
    height: 100vh;
   z-index  : 1; 
	&__heading{
        @include flex ($align-items: center, $justify-content: center);
		flex-direction: column;
		align-items: center;
		text-transform: uppercase;
		// color: $body-color;
		height: 100vh;

	}
	&__title{
        @include font($title-ff, 7rem, $body-color, $fw: 700, $lh: 1.6);
        letter-spacing: .05em;
        z-index: 10;
        margin: 0;
	}
	&__subtitle{
        @include font($title-ff, 1.6rem, $body-color, $fw: 400, $lh: 1.6);
        letter-spacing: .3em;
        z-index: 100;
    }
    & .slick-dots {
		position: absolute;
		top: 35%;
		right: 4%;
	}
}

