.row {
    @include flex ($align-items: center, $justify-content: space-between);
    @media screen and (max-width: 768px) {
        flex-wrap: wrap; 
        justify-content: center;
    }  
    &--reverse {
        flex-direction: row-reverse;
    }
}

.col {
    width: 100%;
    &--1-2--larger{
        max-width: 630px;      
    }
    &--1-2{
        max-width: 500px;      
    }
    @media screen and (max-width: 907px) {
        padding: 40px;
    } 
    @media screen and (max-width: 768px) {
        padding: 20px;
    }  
}



.img__wrap {
    background-color: $grey-color;
    position: relative;
    width: 100%;
    padding-top: 74%;
    overflow: hidden;  
    box-shadow: -140px -70px 0px 0px $blue-color;
    &--reverse {
        box-shadow: 140px -70px 0px 0px $primary-color;
    }
}

.img{
    min-width: 100%;
    min-height: 100%;
    @include posA;
}

.btn{
    display: inline-block;
    background-color: rgba($primary-color, .0);
    border: $border-width solid rgba($secondary-color, 1);
    @include font($btn-ff, 1.4rem, $secondary-color, 400, $lh: 1.8);
    text-transform: uppercase;
    text-decoration: none;
    letter-spacing: .1em;
    padding: 15px 40px;
    text-align: center;
    white-space: nowrap;
    outline: none;
    @include clamp($line-clamp: 1);
    // margin: auto;
    cursor: pointer;
    @include tr;
    @include hover {
        background-color: rgba($primary-color, 1);
        border-color: $primary-color;
        color: $body-color;
        transform: scale(.9) ;  
    }   
}

.news-slider{
	padding: 10px 0 50px;
	display: flex;
		flex-direction: row;
		flex-wrap: nowrap;
		justify-content: space-between;
}

.slide{
	// width: 32%;
}

.card{
    // width: calc(100% / 3 - 20px);
    // width: 360px;
    height: 515px;
    background-color: $body-color;
    outline: 1px solid red;
    margin: 10px;
    flex: 0 1 370px;
    position: relative;
    @include hover{
        box-shadow: 0 0 1px $border-width $grey-color;
        } 
    &__link{
        height: 100%;
        outline: 1px solid orange;
        display: block;
        text-decoration: none;
        padding: $space;  
    }
    &__title{
        @include font($title-ff, 2.2rem, $secondary-color, 700, $lh: 1.5);
        margin: 30px 0 20px 0;
        text-transform: uppercase;
        @include tr;
        @include hover {
            color: $primary-color; 
        } 
        // max-height: 125px;
        @include clamp($line-clamp: 2);
    }
    &__text{
        text-decoration: none;
        @include font($body-ff, 1.8rem, $grey-color, 400, $lh: 1.6);
        @include clamp($line-clamp: 2);
        margin-bottom: 40px;
    }
  
    &__img-wrap{
        position: relative;
        height: 240px;
        overflow: hidden;
        margin: -1 * $space -1 * $space $space;
    }
    &__img{
        @include posA;
        min-width: 100%;
        min-height: 100%;
    }

    &__author{
        text-decoration: none;
        @include posA ($top: null, $left: 0, $delta_x: 0, $delta_y: 0); 
        bottom: -1.5%;
        @include flex ($align-items: center, $justify-content: space-between);
    }

    &__author-img-wrap{
        border-radius: 50%;
        height: 60px;
        width: 60px;
        overflow: hidden;
        position: relative;
        margin: 30px 20px;
    }

    &__author-text{
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    &__author-name{
        @include font($title-ff, 1.8rem, $secondary-color, 400, $lh: 1.2);
        text-transform: uppercase;
        margin: 0 0 10px 0;
    }

    &__post-date{
        @include font($btn-ff, 1.4rem, $blue-color, 400, $lh: 1.6);
        // padding-right: 2rem;
        margin-bottom: auto;
    }
}


