.slick-vertical .slick-slide{border: none;}

.slick-dots {
	list-style: none;
	margin: 0;
	padding: 0;

	& li {
		width: 15px;
		height: 15px;
		border: 1px solid $body-color;
		border-radius: 50%;
		background-color: transparent;
		margin-bottom: 40px;
		cursor: pointer;
		@include tr (all);

		&:hover,
		&:active,
		&.slick-active {
			background-color: $body-color;

		}

	}

	& button {
		display: none;

	}
}



.news-slider {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: space-between;

	& .slick-dots {
		display: flex;
		flex-direction: row;
		position: absolute;
		left: 40%;
		bottom: -3%;

		& li {
			margin: 0 30px 0 0;
		}
	}
}

.slide-news{
	padding: 0 15px;
}

.slick-arrow {
	position: absolute;
	width: 5.5rem;
	height: 5.5rem;
	display: inline-block;
	z-index: 1;
	top: 45%;
	background-color: transparent;
	border: none;
	outline: none;
	font-size: 0;
	@include bg;
	background-size: 100%;
	background-image: url(../img/arrowDown.svg);
	transition: .3s;
	&.slick-prev {
		left: -9%;
		transform: rotate(-180deg);
	}

	&.slick-next {
		right: -9%;

	}

	&:hover,
	&:active {
		background-color: $blue-color;
		border-radius: 50%;
	}
}
    





.footer{
    background-color: $secondary-color;
    padding: 80px 0 60px;
    &__title {
        text-align: center;
        @include flex ($align-items: center, $justify-content: center);
        flex-direction: column;
        margin-bottom: 15px;
    }
 
    &__text {
        @include font($body-ff, 1.6rem, $body-color, 400, $lh: 1.6);
        letter-spacing: .07em;
        text-transform: capitalize;
        @include clamp($line-clamp: 1);
    }
}



